<template>
  <div class="position-relative">
    <!-- Enter phone number -->
    <b-form-input size="sm" placeholder="Enter phone number" v-on:keyup.enter="conference_call(phone_number)" v-model="phone_number" :disabled="message == 'Calling' ? true : false" />
    <span :class="`font-size-12px ${message == 'Calling' ? 'text-primary text-right' : 'text-danger text-right'}`">{{ message }}</span>
    <!-- List view -->
    <b-overlay :show="isLoading" spinner-variant="primary" spinner-small class="position-unset" />
    <b-table fixed responsive small v-if="!isLoading && participants.length > 0" :fields="fields" :items="participants" ref="conference_list_table" class="mb-0 mt-2 conference-th">
      <template #cell(actions)="data">
        <div class="d-flex align-items-center justify-content-end">
          <b-link class="cursor-pointer mr-3" @click="updateParticipant(data.item, data.index, 'muted')" :title="Boolean(participants[data.index].muted) ? 'Mute' : 'Unmute'">
            <i :class="Boolean(participants[data.index].muted) ? 'fe fe-mic-off text-danger' : 'fe fe-mic text-primary'" />
          </b-link>
          <b-link class="cursor-pointer mr-3" @click="updateParticipant(data.item, data.index, 'hold')" title="Hold">
            <i :class="Boolean(participants[data.index].hold) ? 'fe fe-pause-circle text-danger' : 'fe fe-pause-circle text-primary'" />
          </b-link>
          <b-link class="cursor-pointer mr-3" @click="updateParticipant(data.item, data.index, 'hangup')" title="End Call">
            <i class="fe fe-phone text-danger" />
          </b-link>
        </div>
      </template>
    </b-table>
  </div>
</template>

<script>
  import conference from "@/js/dialer/conference";
  export default {
    props: {
      participants: Array,
      conference_sid: String,
      twilioConfig: Object,
      isLoading: Boolean,
      from_number: Number,
    },
    data() {
      return {
        phone_number: "",
        conference_name: "",
        users: [],
        fields: [
          { label: "Number", key: "phone_number" },
          { label: "", key: "actions" },
        ],
        message: "",
      };
    },
    methods: {
      async conference_call(number, callType = "number") {
        this.message = "Calling";
        let form = {
          to_number: number,
          call_type: callType,
          from_number: this.from_number,
        };
        form = { ...form, ...this.twilioConfig };
        try {
          let response = await conference.addParticipant(this.conference_sid, form);
          response.type = "add";
          this.phone_number = "";
          this.message = "";
          this.$emit("updateParticipants", response);
        } catch (e) {
          this.message = "Failed to call";
        }
      },
      async updateParticipant(data, index, action) {
        let value = Boolean(this.participants[index][action]);
        let params = `&${action}=${!value}`;

        let response = await conference.updateParticipant(this.conference_sid, data.call_sid, params, this.twilioConfig);
        response.type = "update";

        // Check for hangup action
        if (action == "hangup") response.type = "delete";

        response.index = index;
        this.$emit("updateParticipants", response);
      },
    },
  };
</script>
<style>
  .conference-th th {
    padding: 5px 7px !important;
  }
  .conference-th td {
    padding: 6px !important;
  }
  .position-unset {
    position: unset;
  }
  .cursor-pointer {
    cursor: pointer !important;
  }
  .conference-th table {
    margin-bottom: 0px !important;
    max-height: 72vh !important;
  }
</style>
