import Data from "../core/data";
import axios from "axios";
import JWT from "./jwt";

const server_url = "https://api.${region}.500apps.com/pbxplus/v1/node/users";

const transfer_url = "https://api.${region}.500apps.com/voxdesk/v1/transfer/${call_type}/outbound/${callsid}/${callType}/${number}";

const conference_url = "https://api.${region}.500apps.com/voxdesk/";

class Conference extends Data {
  /**
   * Method used to get list of users
   */
  async getAgents() {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = server_url;
    url = eval("`" + url + "`");

    let response = await axios.get(url, { headers });
    return response.data;
  }

  /**
   * Method used to transfer call
   * @param {String} callsid - CallSid
   * @param {String} number - Phone Number
   * @param {String} call_type - type cold or warm
   * @param {String} callType - number or client
   */
  // eslint-disable-next-line
  async transferCall(callsid, call_type, callType, number) {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = transfer_url;
    url = eval("`" + url + "`");
    await axios.get(url, { headers });
  }

  debounce(callback, delay) {
    let timer = null;
    return function() {
      clearTimeout(timer);
      var args = arguments;
      let context = this;
      timer = setTimeout(function() {
        callback.apply(context, args);
      }, delay);
    };
  }

  // Get participants in conference
  async getParticipants(conference_sid, twilioConfig) {
    let headers = await JWT.setHeader();

    // eslint-disable-next-line
    let region = this.getRegion();
    let url = conference_url + `conference/participants?conference_sid=${conference_sid}&account_sid=${twilioConfig.ACCOUNT_SID}&auth_token=${twilioConfig.AUTH_TOKEN}`;
    url = eval("`" + url + "`");
    let response = await axios.get(url, { headers });
    return response.data;
  }

  // Add new participant to conference
  async addParticipant(conference_sid, body) {
    let headers = await JWT.setHeader();

    // eslint-disable-next-line
    let region = this.getRegion();
    let url = conference_url + `conference/participant?conference_sid=${conference_sid}`;
    url = eval("`" + url + "`");
    let response = await axios.post(url, body, { headers });

    return response.data;
  }

  // Update existing participants
  async updateParticipant(conference_sid, call_sid, action, body) {
    let headers = await JWT.setHeader();

    // eslint-disable-next-line
    let region = this.getRegion();
    let url = conference_url + `conference/participant?conference_sid=${conference_sid}&call_sid=${call_sid}${action}`;
    url = eval("`" + url + "`");
    let response = await axios.put(url, body, { headers });
    return response.data;
  }
}

export default new Conference();
